import "../buscar_guias/buscar_guias.css";
import React, { useState } from "react";
// import { useSelector } from "react-redux";
import { ParseDate } from "../../../../helpers/helper";
import Spinner from "../../../spinner/spinner";
import Dashfooter from "../../../dash-componets/footter/dashfooter";
import { UseFormDardeBajaGuias } from "../../../../hooks/user_from_buscar_guias_dardebaja";
// import { listTextGuias } from "../../../../constants/constants";
import EstructuraGuia from "../../../estructura-guia-imprimir/estructura-print";
import ReactHTMLTableToExcel from "react-html-table-to-excel";
import logo from "../../../../img/logoRapifas.png";
import { DebounceInput } from "react-debounce-input";
import {
  generateGuiatosearch,
  generateGuiatosearchtoPrint,
  generateListGuias,
} from "../../../../helpers/estructura-pdf";
import { guiatest, listTextGuias } from "../../../../constants/constants";

function BuscarGuias() {
  const [
    values,
    fetching,
    listGuias,
    CurrentGuia,
    darDeBaja,
    getGuiaSearchAuto,
    solicitarAnulacion,
    handlerChangeText,
    getGuiaSearch,
    showDetails,
    dowloadGuia,
    printGuiaAction,
    motivohandleChange,
    onPressEnter,
    // currentPrintGuia,
    // printGuiaNmed,
  ] = UseFormDardeBajaGuias();

  const [autoSearch, setautoSearch] = useState(false);
  const onclickSwitch = () => {
    setautoSearch(!autoSearch);
  };


  return (
    <div className="crearGuias col-md-9 ms-sm-auto col-lg-10 px-md-4 pt-4 scroll-section">
      <div className="container-fluid pt-4 d-print-none ">
        <h4 className="title-separtor">Rastrear Guía</h4>
        <hr />
        <div className="card card-body">
          <div className="row align-items-center mb-4">
            <div className="col-sm-12 col-md-6">
              <label htmlFor="simpleinput" className="form-label-input">
                Buscar Por
              </label>
              <select
                className="form-select"
                value={values?.busqueda}
                onChange={handlerChangeText}
                name="busqueda"
              >
                <option value="guia">Número de Guía</option>
                <option value="cedula_destinatario">
                  Cédula / RUC Destinatario
                </option>
                <option value="cedula_remitente">Cédula / RUC Remitente</option>
              </select>
            </div>

            <div className="col-sm-12 col-md-6">
              <form onSubmit={getGuiaSearch}>
                <div className="row">
                  <div className="col-sm-12 col-md-8">
                    <label className="form-label-input">Buscar</label>
                  </div>
                  <div className="col-sm-12 col-md-4 text-start">
                    <div className="form-check form-switch">
                      <label className="form-check-label">
                        <span className="text-muted"> Automático</span>
                      </label>
                      <input
                        className="form-check-input"
                        type="checkbox"
                        id="flexSwitchCheckChecked"
                        checked={autoSearch}
                        onChange={onclickSwitch}
                      />
                    </div>
                  </div>
                </div>
                {/* <label className="form-label-input">Buscar</label> */}
                <div className="input-group">
                  {autoSearch ? (
                    <DebounceInput
                      minLength={0}
                      debounceTimeout={500}
                      type="text"
                      className="form-control dropdown-toggle"
                      placeholder="Buscar ..."
                      id="top-search"
                      name="textSearch"
                      value={values?.textSearch}
                      onChange={(e) => getGuiaSearchAuto(e.target.value)}
                      autoFocus={true}
                    />
                  ) : (
                    <input
                      type="text"
                      className="form-control dropdown-toggle"
                      placeholder="Buscar ..."
                      id="top-search"
                      name="textSearch"
                      value={values?.textSearch}
                      onChange={handlerChangeText}
                      autoFocus={true}
                    />
                  )}
                  <span className="mdi mdi-magnify search-icon" />
                  {fetching ? (
                    <div className="container-spinner-buscar">
                      <Spinner />
                    </div>
                  ) : autoSearch ? (
                    <></>
                  ) : (
                    <button
                      className="btn-solid-secondary px-4 "
                      type="button"
                      onClickCapture={getGuiaSearch}
                    >
                      Buscar
                    </button>
                  )}
                </div>
              </form>
            </div>
          </div>
        </div>
        <div className="card card-body">
          <div className="col-sm-12 col-md-6">
            <h5 className="titleSection-form">Listado de Guias</h5>
          </div>
          <hr />
          <div className="row align-items-start">
            <div className="row table-cm table-responsive">
              <table className="table  nowrap">
                <thead>
                  <tr className="header-table">
                    <th>ID</th>
                    <th
                      className="text-truncate"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="Estado"
                    >
                      Estado
                    </th>
                    <th
                      className="text-truncate"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="Fecha"
                    >
                      Fecha
                    </th>
                    <th
                      className="text-truncate"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="Origen"
                    >
                      Origen
                    </th>
                    <th
                      className="text-truncate"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="Destino"
                    >
                      Destino
                    </th>

                    <th
                      className="text-truncate"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="Acciones"
                    >
                      Acciones
                    </th>
                  </tr>
                </thead>

                <tbody>
                  {/* {listTextGuias?.map((guia) => ( */}
                  {listGuias?.map((guia) => {
        
                    return (
                      <tr key={guia?.idGuia} className="hover-table">
                        <td
                          className="text-truncate"
                          data-toggle="tooltip"
                          data-placement="top"
                          title={guia?.idGuia}
                        >
                          {guia?.idGuia}
                        </td>
                        <td
                          className="text-truncate"
                          data-toggle="tooltip"
                          data-placement="top"
                          title={
                            guia?.estado === null || guia?.estado === ""
                              ? "-----"
                              : guia?.estado
                          }
                        >
                          {guia?.estado === null || guia?.estado === ""
                            ? "-----"
                            : guia?.estado}
                        </td>
                        <td
                          className="text-truncate"
                          data-toggle="tooltip"
                          data-placement="top"
                          title={ParseDate(guia?.fechaActual)}
                        >
                          {ParseDate(guia?.creacion?.fecha)}
                        </td>
                        <td
                          className="text-truncate"
                          data-toggle="tooltip"
                          data-placement="top"
                          title={`${guia?.remitente?.provincia}-${guia?.remitente?.canton}`}
                        >
                          {`${guia?.remitente?.provincia}-${guia?.remitente?.canton}`}
                        </td>
                        <td
                          className="text-truncate"
                          data-toggle="tooltip"
                          data-placement="top"
                          title={`${guia?.destinatario?.provincia}-${guia?.destinatario?.canton}`}
                        >
                          {`${guia?.destinatario?.provincia}-${guia?.destinatario?.canton}`}
                        </td>

                        <td className="text-truncate">
                          <a
                            className="actions view"
                            type="button"
                            data-toggle="tooltip"
                            data-placement="top"
                            title="Ver Detalles"
                            onClick={(e) => showDetails(true, guia, e)}
                          >
                            <i className="material-icons">remove_red_eye</i>
                          </a>

                          <a
                            className="actions print"
                            type="button"
                            data-toggle="tooltip"
                            data-placement="top"
                            title="Imprimir"
                            onClick={() => printGuiaAction(guia)}
                          >
                            <i className="material-icons">local_printshop</i>
                          </a>
                        </td>
                      </tr>
                    );
                  })}
                </tbody>
              </table>

              <hr />
              {listGuias.length === 0 ? (
                <div>
                  <p>No hay items en la lista</p>
                </div>
              ) : (
                <div>
                  <ReactHTMLTableToExcel
                    className="btn btn-success w-100"
                    table="table-to-excel"
                    filename="Listado de guias"
                    sheet="Guias"
                    buttonText="Descargar Reporte"
                  />
                </div>
              )}
            </div>
          </div>
        </div>

        <h4 className="title-separtor">Detalles</h4>
        <hr />

        {values?.shoDetail ? (
          <div className="form">
            {CurrentGuia?.idGuia === undefined ? (
              <></>
            ) : (
              <div className="card card-body">
                <div className="text-end mb-4">
                  <button
                    type="button"
                    class="btn-close "
                    aria-label="Close"
                    onClick={() => showDetails(false, null)}
                  ></button>
                </div>
                {generateGuiatosearch(CurrentGuia).map((guia) => {
                  return (
                    <EstructuraGuia
                      guia={guia}
                      showLine={!guia?.islast}
                      showSpace={guia?.showSpace}
                    />
                  );
                })}
                {/* <EstructuraGuia
                  guia={CurrentGuia}
                  showLine={false}
                  showSpace={false}
                  showMarca={false}
                /> */}
                {CurrentGuia?.estado === "creado" ? (
                  <div className="row mt-4">
                    <div className="col">
                      <button
                        type="button"
                        className="col m-2 btn btn-danger w-100"
                        data-bs-toggle="modal"
                        data-bs-target="#anularguia"
                      >
                        Solicitar Anulación
                      </button>
                    </div>
                  </div>
                ) : (
                  <></>
                )}
              </div>
            )}

            <div
              className="modal fade"
              id="anularguia"
              data-bs-backdrop="static"
              data-bs-keyboard="false"
              aria-labelledby="staticBackdropLabel"
              aria-hidden="true"
            >
              <div className="modal-dialog modal-dialog-centered">
                <div className="modal-content">
                  <div className="modal-header">
                    <h5 className="modal-title" id="staticBackdropLabel">
                      Solicitar Anulación de guía
                    </h5>
                    <button
                      type="button"
                      className="btn-close"
                      data-bs-dismiss="modal"
                      aria-label="Close"
                    ></button>
                  </div>

                  <div className="modal-body">
                    <p>
                      Esta seguro de solicitar la anulación de esta guía, una
                      vez solicitada no se podra cambiar este estado.
                    </p>
                    <input
                      type="text"
                      placeholder="Ingrese el motivo de anulación"
                      className="form-control"
                      onChange={motivohandleChange}
                      value={values?.motivoAnulacion}
                      name="motivoAnulacion"
                      required
                    />
                  </div>

                  <div className="modal-footer">
                    <button
                      type="button"
                      className="btn btn-secondary"
                      data-bs-dismiss="modal"
                      onClick={() => {
                        showDetails(false, null);
                      }}
                    >
                      Cancelar
                    </button>
                    <button
                      type="submit"
                      className=" btn btn-danger p-2"
                      data-bs-dismiss="modal"
                      onClick={solicitarAnulacion}
                    >
                      Enviar Solicitud
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        ) : (
          <></>
        )}
      </div>
      <div
        className="modal fade"
        id="exampleModal"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="exampleModalLabel">
                Generar Guía
              </h5>
              <button
                type="button"
                className="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>
            <div className="modal-body">...</div>
            <div className="modal-footer">
              <button type="button" className="btn btn-primary">
                Guardar PDF
              </button>
              <button type="button" className="btn btn-secondary">
                Imprimir Guias
              </button>
            </div>
          </div>
        </div>
      </div>
      <Dashfooter />
      {/* showtoSalve
            ? "guias-print d-print-block"
            : "guias-print d-none d-print-block" */}
      {CurrentGuia?.remitente ? (
         <div id="guias-export" className="guias-print d-none d-print-block">
         {generateGuiatosearchtoPrint(CurrentGuia).map((guiaPrint) => {
           return (
             <div className="section-print-guias">
               <EstructuraGuia
                 guia={guiaPrint}
                 showLine={!guiaPrint?.islast}
                 showSpace={guiaPrint?.showSpace}
               />
             </div>
           );
         })}
       </div> 
      ) : (
        <></>
      )}

      {/* =============================== TABLE A EXPORTAR EN EXCEL ============= */}
      {listGuias.length > 0 ? (
        <table className="table" id="table-to-excel">
          <tr className="hover-table">
            <td
              className="text-truncate"
              data-toggle="tooltip"
              data-placement="top"
            ></td>
          </tr>
          <tr className="hover-table">
            <td
              className="text-truncate"
              data-toggle="tooltip"
              data-placement="top"
            >
              Fecha:
            </td>
            <td
              className="text-truncate"
              data-toggle="tooltip"
              data-placement="top"
            >
              {ParseDate(Date.now())}
            </td>
          </tr>
          <tr className="hover-table">
            <td
              className="text-truncate"
              data-toggle="tooltip"
              data-placement="top"
            ></td>
          </tr>
          <tr className="hover-table">
            <td
              className="text-truncate"
              data-toggle="tooltip"
              data-placement="top"
            ></td>
          </tr>
          <thead>
            <tr className="header-table">
              <th>ID</th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Estado"
              >
                Estado
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Fecha"
              >
                Fecha
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Origen"
              >
                Origen
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                Destino
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                Contenido
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                Creado por
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                Nombre de usuario creador
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                Canton de destino
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                Código postal de destino
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                Dirección de destino
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                Email de destino
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                Nombre destinatario
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                Parroquia de destino
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                Provincia de destino
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                Razon social de destino
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                Referencia de destino
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                RUC destinatario
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                Teléfono 1 destinatario
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                Teléfono 2 destinatario
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                Teléfono 3 destinatario
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                Motivo
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                Número Piezas
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                Alto Contendo
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                Ancho de contenido
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                Largo de contenido
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                Peso total de contenido
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                Cantón de remitente
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                Código postal de remitente
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                Dirección remitente
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                Email Remitente
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                Parroquia remitente
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                Provincia remitente
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                Razón social remitente
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                Referencia remitente
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                Responsable remitente
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                RUC remitente
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                Teléfono de empresa remitente
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                Teléfono responsable remitente
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                Tipo
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                Peso actualizado por
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                Fecha actualización de peso
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                Actualizado por:
              </th>
            </tr>
          </thead>

          <tbody>
            {/* {listTextGuias?.map((guia) => ( */}
            {listGuias?.map((guia) => {

              return (
                <tr key={guia?.idGuia} className="hover-table">
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={guia?.idGuia}
                  >
                    {guia?.idGuia}
                  </td>
                  <td
                    className="text-truncate"
                  >
                    {guia?.estado === null || guia?.estado === ""
                      ? "-----"
                      : guia?.estado}
                  </td>
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={ParseDate(guia?.creacion?.fecha)}
                  >
                    {ParseDate(guia?.creacion?.fecha)}
                  </td>
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={`${guia?.remitente?.provincia}-${guia?.remitente?.canton}`}
                  >
                    {`${guia?.remitente?.provincia}-${guia?.remitente?.canton}`}
                  </td>
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={`${guia?.destinatario?.provincia}-${guia?.destinatario?.canton}`}
                  >
                    {`${guia?.destinatario?.provincia}-${guia?.destinatario?.canton}`}
                  </td>

                  <td className="text-truncate">{guia?.contenido}</td>
                  <td className="text-truncate">{guia?.creadoPor}</td>
                  <td className="text-truncate">{guia?.creadoPorUsuario}</td>
                  <td className="text-truncate">
                    {guia?.destinatario?.canton}
                  </td>
                  <td className="text-truncate">
                    {guia?.destinatario?.codigoPostal}
                  </td>
                  <td className="text-truncate">
                    {guia?.destinatario?.direccion}
                  </td>
                  <td className="text-truncate">{guia?.destinatario?.email}</td>
                  <td className="text-truncate">
                    {guia?.destinatario?.nombres}
                  </td>
                  <td className="text-truncate">
                    {guia?.destinatario?.parroquia}
                  </td>
                  <td className="text-truncate">
                    {guia?.destinatario?.provincia}
                  </td>
                  <td className="text-truncate">
                    {guia?.destinatario?.razonSocial}
                  </td>
                  <td className="text-truncate">
                    {guia?.destinatario?.referencia}
                  </td>
                  <td className="text-truncate">{guia?.destinatario?.ruc}</td>
                  <td className="text-truncate">
                    {guia?.destinatario?.telefono1}
                  </td>
                  <td className="text-truncate">
                    {guia?.destinatario?.telefono2}
                  </td>
                  <td className="text-truncate">
                    {guia?.destinatario?.telefono3}
                  </td>
                  <td className="text-truncate">{guia?.motivo}</td>
                  <td className="text-truncate">{guia?.numPiezas}</td>
                  <td className="text-truncate">{guia?.peso?.alto}</td>
                  <td className="text-truncate">{guia?.peso?.ancho}</td>
                  <td className="text-truncate">{guia?.peso?.largo}</td>
                  <td className="text-truncate">{guia?.peso?.pesototal}</td>
                  <td className="text-truncate">{guia?.remitente?.canton}</td>
                  <td className="text-truncate">
                    {guia?.remitente?.codigoPostal}
                  </td>
                  <td className="text-truncate">
                    {guia?.remitente?.direccion}
                  </td>
                  <td className="text-truncate">{guia?.remitente?.email}</td>
                  <td className="text-truncate">
                    {guia?.remitente?.parroquia}
                  </td>
                  <td className="text-truncate">
                    {guia?.remitente?.provincia}
                  </td>
                  <td className="text-truncate">
                    {guia?.remitente?.razonSocial}
                  </td>
                  <td className="text-truncate">
                    {guia?.remitente?.referencia}
                  </td>
                  <td className="text-truncate">
                    {guia?.remitente?.responsable}
                  </td>
                  <td className="text-truncate">{guia?.remitente?.ruc}</td>
                  <td className="text-truncate">
                    {guia?.remitente?.telefonoEmpresa}
                  </td>
                  <td className="text-truncate">
                    {guia?.remitente?.telefonoResponsable}
                  </td>
                  <td className="text-truncate">{guia?.tipo}</td>
                  <td className="text-truncate">
                    {guia?.updatePeso?.actualizadoPor}
                  </td>
                  <td className="text-truncate">
                    {ParseDate(guia?.updatePeso?.fechaPesoActualizado)}
                  </td>
                  <td className="text-truncate">{guia?.updatePeso?.rol}</td>
                </tr>
              );
            })}
          </tbody>
        </table>
      ) : (
        <></>
      )}
    </div>
  );
}

export default BuscarGuias;
