import { useState } from "react";
import { useToasts } from "react-toast-notifications";
import { useDispatch, useSelector } from "react-redux";
import Swal from "sweetalert2";
import { v1 as uuid } from "uuid";
import {
  addGuiaManifiestos,
  addIdGuiaManifiesto,
  autoguiaManifiesto,
  cleanManifiesto,
  crearManifiesto,
  deleteGuiaManifiesto,
  deleteguiaSearch,
  deleteIdGuiaManifiesto,
  findGuiaManifiesto,
  setDestion,
  setDestiono,
} from "../redux/actions/manifiestos_actions";
import { removeSpaces, returnTotalGuias } from "../helpers/helper";

function CrearManifiestoHook(initialValues) {
  const {
    manifiestos: {
      listGuiasManifiesto,
      fetching,
      provinciaDestino,
      idGuiasManifiesto,
      manifiestoPrint,
      autosearchGuiaManifieto,
    },
    auth: { dataProfile },
  } = useSelector((state) => state);
  const dispatch = useDispatch();
  const { addToast } = useToasts();

  const rolUser =
    dataProfile?.rol === "admin"
      ? "Administrador"
      : dataProfile?.rol === "puntoVenta"
      ? "Punto de venta"
      : dataProfile?.rol === "sucursal"
      ? "Sucursal"
      : "Corporativo";

  const [showErrorDestino, setshowErrorDestino] = useState(false);
  const [values, setValue] = useState({});

  const swalWithBootstrapButtons = Swal.mixin({
    customClass: {
      confirmButton: "btn btn-success",
      cancelButton: "btn btn-danger",
    },
    buttonsStyling: false,
  });

  const showAlet = (provincia, guia) => {
    return swalWithBootstrapButtons
      .fire({
        title: "Quieres Agregar esta Guía?",
        text: `Esta guía tiene destino a la provincia de ${provincia}, y el manifiesto a ${provinciaDestino}, Esta seguro de agregarla ?`,
        icon: "warning",
        showCancelButton: true,
        confirmButtonText: "Agregar al Manifiesto",
        cancelButtonText: "No",
        reverseButtons: true,
      })
      .then((result) => {
        if (result.isConfirmed) {
          dispatch(addGuiaManifiestos(guia));
          toastSms("Guia Agregada", "success");
        }
      });
  };

  const showAletDelete = (guia) => {
    return swalWithBootstrapButtons
      .fire({
        title: "Estas seguro de eliminar esta guía?",
        text: `Esta guía se eliminará permanentemente de esta la lista, estas seguro`,

        showCancelButton: true,
        confirmButtonText: "Eliminar",
        cancelButtonText: "Cancelar",
        reverseButtons: true,
      })
      .then((result) => {
        if (result.isConfirmed) {
          dispatch(deleteGuiaManifiesto(guia.idGuia));
          toastSms("Guia eliminada", "error");
        }
      });
  };

  const toastSms = (text, type) => {
    return addToast(text, {
      appearance: type,
      autoDismiss: true,
      autoDismissTimeout: 2000,
    });
  };
  const comprobarGuia = (provincia, guia) => {
    if (provinciaDestino === provincia) {
      dispatch(addGuiaManifiestos(guia));
      toastSms("Guia Agregada", "success");
    } else {
      showAlet(provincia, guia);
    }
  };
  const autoSearchGuias = (e, auto) => {
    setValue({ idGuia: removeSpaces(e.target.value) });
    if (e.target.value !== "") {
      e.preventDefault();
    }
    if (provinciaDestino === "") {
      setshowErrorDestino(false);
      toastSms("Es necesario agregar un destino", "info");
    } else {
      let listResividos = listGuiasManifiesto.filter(
        (item) => item?.idGuia === removeSpaces(e.target.value)
      ).length;
      if (listResividos >= 1) {
        toastSms("Esta guia ya fue agregada", "info");
      } else {
        if (auto && auto === true) {
          const errorsms = (sms) => toastSms(`${sms}`, "error");
          dispatch(
            findGuiaManifiesto(
              removeSpaces(e.target.value),
              errorsms,
              comprobarGuia
            )
          );
        }
      }
    }
  };

  const manualSearcGuias = (e) => {
    e.preventDefault();
    const errorsms = (sms) => toastSms(`${sms}`, "error");
    dispatch(findGuiaManifiesto(values?.idGuia, errorsms, comprobarGuia));
  };

  const setDestinoReducer = (e) => {
    if (e.target.value !== "") {
      e.preventDefault();
    }
    return dispatch(setDestion(e.target.value));
  };

  const deleteGuia = (guia) => {
    return showAletDelete(guia);
  };
  const printGuias = () => {
    window.print();
  };
  const GuardarManifiesto = () => {
    let manifiesto = generateManifiesto();
    const errorsms = (sms) => toastSms(`${sms}`, "error");
    const successSms = (sms) => toastSms(`${sms}`, "success");
    let dataUsertemp = {
      usuario: dataProfile?.email,
      rol: rolUser?.toString(),
    }
    dispatch(crearManifiesto(manifiesto, successSms, errorsms, printGuias,dataUsertemp ));
  };

  const generateManifiesto = () => {
    let tempGuiaManifiesto = {
      estado: "creado",
      idManifiesto: initialValues.idManifiesto,
      listGuias: [],
      fechaCreacion: Date.now(),
      creadoPor: dataProfile?.email,
      rol: rolUser,
      destino: provinciaDestino,
    };
    listGuiasManifiesto.map((item) => {
      tempGuiaManifiesto = {
        ...tempGuiaManifiesto,
        listGuias: [
          ...tempGuiaManifiesto.listGuias,
          {
            estado: "creado",
            idGuia: item?.idGuia,
            peso: returnTotalGuias(item?.piezas, item?.numPiezas),
          },
        ],
      };
    });
    return tempGuiaManifiesto;
  };
  const changeSearch = () => {
    dispatch(autoguiaManifiesto(!autosearchGuiaManifieto));
  };
  return [
    autoSearchGuias,
    setDestinoReducer,
    deleteGuia,
    GuardarManifiesto,
    generateManifiesto,
    changeSearch,
    manualSearcGuias,
    showErrorDestino,
    provinciaDestino,
    listGuiasManifiesto,
    fetching,
    manifiestoPrint,
    autosearchGuiaManifieto,
  ];
}
export default CrearManifiestoHook;
