import "../actualizar_pesos/actualizarPesos.css";
import React, { useEffect } from "react";
import { ParseDate, returnTotalGuias } from "../../../../helpers/helper";
import Spinner from "../../../spinner/spinner";
import Dashfooter from "../../../dash-componets/footter/dashfooter";
import ActualizarPesosHook from "../../../../hooks/Actualizar_Pesos_Hook";
import { DebounceInput } from "react-debounce-input";
import FormPesos from "./from_update_pesos";

function ActualizarPesos() {
  const [
    ListGuiasParaPeso,
    fetching,
    currentDetailGuiaPesos,
    ListGuiasPesoActualizado,
    values,
    autosearchPesos,
    searchGuiaId,
    changeAutosearch,
    agregarAListaActualizados,
    ActivePesoVol,
    handleChangeValue,
    editGuiaPeso,
    updateGuiaonList,
    eliminardeListaActualizados,
    updateGuiasFirebase,
    handleOnBlur,
    handrerchangeSearch,
  ] = ActualizarPesosHook();

  const { idGuia } = currentDetailGuiaPesos;

  useEffect(() => {
    // // console.log("LISTA DE PESOS");
    // // console.log(ListGuiasParaPeso);
    // // console.log(ListGuiasParaPeso);
    if (ListGuiasParaPeso && Object.keys(ListGuiasParaPeso).length > 0) {
      document.getElementById("input-peso0").focus();
    }
  }, [ListGuiasParaPeso]);

  return (
    <div className="crearGuias col-md-9 ms-sm-auto col-lg-10 px-md-4 pt-4 scroll-section">
      <div className="container-fluid pt-4">
        <h4 className="title-separtor">Actualizar Pesos</h4>
        <hr />
        <p>{idGuia}</p>
        <div className="card card-body">
          <div className="row align-items-center">
            <div className="col-sm-12 col-md-12">
              <form onSubmit={handleOnBlur}>
                <div className="row">
                  <div className="col-sm-12 col-md-9">
                    <label className="form-label-input">Buscar Guía</label>
                  </div>
                  {/* <div className="col-sm-12 col-md-3 ">
                    <div className="form-check form-switch">
                      <span className="text-muted text-start">
                        Busqueda Automática
                      </span>
                      <input
                        className="form-check-input text-start"
                        type="checkbox"
                        id="flexSwitchCheckChecked"
                        checked={autosearchPesos}
                        onChange={changeAutosearch}
                      />
                    </div>
                  </div> */}
                </div>
                <div className="input-group">
                  <span className="input-group-text" id="basic-addon1">
                    {fetching ? (
                      <div
                        className="spinner-border spinner-border-sm"
                        role="status"
                      >
                        <span className="sr-only">Loading...</span>
                      </div>
                    ) : (
                      <i className="material-icons">search</i>
                    )}
                  </span>
                  {autosearchPesos ? (
                    <DebounceInput
                      minLength={9}
                      debounceTimeout={500}
                      type="text"
                      className="form-control dropdown-toggle"
                      placeholder="Ingresar Num. de Guía"
                      id="searchGuia"
                      name="idGuia"
                      onChange={handleOnBlur}
                      value={values?.idGuia}
                    />
                  ) : (
                    <input
                      type="text"
                      className="form-control dropdown-toggle"
                      placeholder="Ingresar Num. de Guía"
                      id="searchGuia"
                      name="idGuia"
                      onChange={handrerchangeSearch}
                      value={values?.idGuia}
                    />
                  )}
                  <span className="mdi mdi-magnify search-icon" />
                  {fetching ? (
                    <div className="container-spinner-buscar">
                      <Spinner />
                    </div>
                  ) : autosearchPesos ? (
                    <></>
                  ) : (
                    <button
                      className="btn-solid-secondary px-4 "
                      type="button"
                      onClickCapture={searchGuiaId}
                    >
                      Buscar
                    </button>
                  )}
                </div>
                <div className="col text-start mt-2">
                    <div className="form-check form-switch">
                      Busqueda Automatica
                      <input
                        className="form-check-input"
                        type="checkbox"
                        id="flexSwitchCheckChecked"
                        checked={autosearchPesos}
                        onChange={changeAutosearch}
                      />
                    </div>
                  </div>
              </form>
            </div>
          </div>
        </div>
        <h5 className="titleSection-form mt-4">Guia Seleccionada</h5>
        <hr />
        <div className="card card-body">
          <table className="table dt-responsive nowrap">
            <thead>
              <tr className="header-table">
                <th
                  className="overflow-text"
                  data-bs-toggle="tooltip"
                  data-bs-placement="bottom"
                  title="Num. Guia"
                >
                  ID
                </th>
                <th>Fecha</th>
                <th>Origen</th>
                <th>Destino</th>
                <th>Peso</th>
                <th>Piezas</th>
                <th>Tipo</th>
                <th>Estado</th>
                {/* <th
                  className="overflow-text"
                  data-bs-toggle="tooltip"
                  data-bs-placement="bottom"
                  title="Ver Detalles"
                >
                  Detalles
                </th> */}
              </tr>
            </thead>

            <tbody>
              {ListGuiasParaPeso?.idGuia === undefined ? (
                <></>
              ) : (
                <tr
                  key={`consultados_${ListGuiasParaPeso?.idGuia}`}
                  className="hover-table"
                >
                  <td
                    className="overflow-text"
                    data-bs-toggle="tooltip"
                    data-bs-placement="bottom"
                    title={ListGuiasParaPeso?.idGuia}
                  >
                    {ListGuiasParaPeso?.idGuia}
                  </td>
                  <td
                    className="overflow-text"
                    data-bs-toggle="tooltip"
                    data-bs-placement="bottom"
                    title={ParseDate(ListGuiasParaPeso?.creacion?.fecha)}
                  >
                    {ParseDate(ListGuiasParaPeso?.creacion?.fecha)}
                  </td>
                  <td
                    className="overflow-text"
                    data-bs-toggle="tooltip"
                    data-bs-placement="bottom"
                    title={`${ListGuiasParaPeso?.remitente.provincia}-${ListGuiasParaPeso?.remitente.canton}`}
                  >
                    {`${ListGuiasParaPeso?.remitente.provincia}-${ListGuiasParaPeso.remitente.canton}`}
                  </td>
                  <td
                    className="overflow-text"
                    data-bs-toggle="tooltip"
                    data-bs-placement="bottom"
                    title={`${ListGuiasParaPeso?.destinatario.provincia}-${ListGuiasParaPeso?.destinatario.canton}`}
                  >
                    {`${ListGuiasParaPeso?.destinatario.provincia}-${ListGuiasParaPeso?.destinatario.canton}`}
                  </td>
                  <td
                    className="overflow-text"
                    data-bs-toggle="tooltip"
                    data-bs-placement="bottom"
                    title={returnTotalGuias(
                      ListGuiasParaPeso?.piezas,
                      ListGuiasParaPeso?.numPiezas
                    )}
                  >
                    {returnTotalGuias(
                      ListGuiasParaPeso?.piezas,
                      ListGuiasParaPeso?.numPiezas
                    )}
                  </td>
                  <td
                    className="overflow-text"
                    data-bs-toggle="tooltip"
                    data-bs-placement="bottom"
                    title={ListGuiasParaPeso?.numPiezas}
                  >
                    {ListGuiasParaPeso?.numPiezas}
                  </td>
                  <td
                    className="overflow-text"
                    data-bs-toggle="tooltip"
                    data-bs-placement="bottom"
                    title={`${
                      ListGuiasParaPeso?.tipo === "paquete"
                        ? "PAQUETERIA"
                        : "DOCUMENTO"
                    }`}
                  >
                    {`${
                      ListGuiasParaPeso?.tipo === "paquete"
                        ? "PAQUETERIA"
                        : "DOCUMENTO"
                    }`}
                  </td>
                  <td
                    className="overflow-text"
                    data-bs-toggle="tooltip"
                    data-bs-placement="bottom"
                    title={`${ListGuiasParaPeso?.estado}`}
                  >
                    {`${ListGuiasParaPeso?.estado}`}
                  </td>
                </tr>
              )}
            </tbody>
          </table>

          {ListGuiasParaPeso?.idGuia === undefined ? (
            <p className="text-muted">No hay guías en la lista</p>
          ) : (
            <></>
          )}
        </div>
        {ListGuiasParaPeso.piezas === undefined ? (
          <></>
        ) : (
          <div>
            <h5 className="titleSection-form mt-2">Actualizar Peso</h5>
            <hr />
            <form
              onSubmit={
                values?.edit === true
                  ? updateGuiaonList
                  : agregarAListaActualizados
              }
            >
              <FormPesos
                numPiezas={ListGuiasParaPeso?.numPiezas}
                piezasdata={values?.piezas}
                values={values}
                agregarAListaActualizados={agregarAListaActualizados}
                ActivePesoVol={ActivePesoVol}
                handleChangeValue={handleChangeValue}
              />
              {ListGuiasParaPeso?.numPiezas > 1 ? (
                <div className="card card-body">
                  <button className="btn btn-success w-100">
                    Guardar Pesos
                  </button>
                </div>
              ) : (
                <></>
              )}
            </form>
          </div>
        )}
        {values?.editpesos === true ? (
          <div>
            <h5 className="titleSection-form mt-2">Editar Pesos</h5>
            <hr />
            <form onSubmit={updateGuiaonList}>
            
               <FormPesos
                numPiezas={values?.piezas?.length}
                piezasdata={values?.piezas}
                values={values}
                agregarAListaActualizados={agregarAListaActualizados}
                ActivePesoVol={ActivePesoVol}
                handleChangeValue={handleChangeValue}
              />
              {values?.guia?.numPiezas > 1 ? (
                <div className="card card-body">
                  <button className="btn btn-primary w-100">
                    Editar Pesos
                  </button>
                </div>
              ) : (
                <></>
              )}
            </form>
          </div>
        ) : (
          <></>
        )}
        <div className="card card-body">
          <h5 className="titleSection-form mt-2">Guias con Peso Actualizado</h5>
          <hr />
          <div className="row align-items-start">
            <div className="row table-cm">
              <table className="table dt-responsive nowrap">
                <thead>
                  <tr className="header-table">
                    <th
                      className="overflow-text"
                      data-bs-toggle="tooltip"
                      data-bs-placement="bottom"
                      title="Num. Guia"
                    >
                      ID
                    </th>
                    <th>Origen</th>
                    <th>Destino</th>
                    <th>Peso</th>
                    <th>Piezas</th>
                    <th>Tipo</th>
                    <th
                      className="overflow-text"
                      data-bs-toggle="tooltip"
                      data-bs-placement="bottom"
                      title="Ver Detalles"
                    >
                      Editar
                    </th>
                    <th
                      className="overflow-text"
                      data-bs-toggle="tooltip"
                      data-bs-placement="bottom"
                      title="Ver Detalles"
                    >
                      Eliminar
                    </th>
                  </tr>
                </thead>

                <tbody>
                  {ListGuiasPesoActualizado?.map((guia) => (
                    <tr
                      key={`actualizados_${guia.idGuia}`}
                      className="hover-table"
                    >
                      <td
                        className="overflow-text"
                        data-bs-toggle="tooltip"
                        data-bs-placement="bottom"
                        title={guia.idGuia}
                      >
                        {guia.idGuia}
                      </td>
                      <td
                        className="overflow-text"
                        data-bs-toggle="tooltip"
                        data-bs-placement="bottom"
                        title={`${guia.remitente.provincia}-${guia.remitente.canton}`}
                      >
                        {`${guia.remitente.provincia}-${guia.remitente.canton}`}
                      </td>
                      <td
                        className="overflow-text"
                        data-bs-toggle="tooltip"
                        data-bs-placement="bottom"
                        title={`${guia.destinatario.provincia}-${guia.destinatario.canton}`}
                      >
                        {`${guia.destinatario.provincia}-${guia.destinatario.canton}`}
                      </td>
                      <td
                        className="overflow-text"
                        data-bs-toggle="tooltip"
                        data-bs-placement="bottom"
                        title={returnTotalGuias(guia?.piezas, guia?.numPiezas)}
                      >
                        {returnTotalGuias(guia?.piezas, guia?.numPiezas)} Kg
                      </td>
                      <td
                        className="overflow-text"
                        data-bs-toggle="tooltip"
                        data-bs-placement="bottom"
                        title={`${guia?.numPiezas}`}
                      >
                        {guia?.numPiezas}
                      </td>
                      <td
                        className="overflow-text"
                        data-bs-toggle="tooltip"
                        data-bs-placement="bottom"
                        title={`${guia?.tipo}`}
                      >
                        <b>
                          {guia?.tipo === "paquete"
                            ? "PAQUETERIA"
                            : "DOCUMENTO"}
                        </b>
                      </td>

                      <td>
                        <div
                          type="button"
                          className="w-100 "
                          onClick={() => editGuiaPeso(guia)}
                          data-bs-toggle="tooltip"
                          data-bs-placement="bottom"
                          title="Ver Detalles"
                        >
                          <i className="material-icons">mode_edit</i>
                        </div>
                      </td>
                      <td>
                        <div
                          type="button"
                          className="w-100 "
                          onClick={() => eliminardeListaActualizados(guia)}
                          data-bs-toggle="tooltip"
                          data-bs-placement="bottom"
                          title="Eliminar de la lista"
                        >
                          <i className="material-icons">delete_forever</i>
                        </div>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
            <div className="row">
              {ListGuiasPesoActualizado.length === 0 ? (
                <p className="text-muted">No hay guías en la lista</p>
              ) : (
                <></>
              )}
              {ListGuiasPesoActualizado.length >= 1 ? (
                <button
                  type="button"
                  className=" btn-dt-details col mx-1 bt-a p-2"
                  onClick={updateGuiasFirebase}
                >
                  Guardar Datos
                </button>
              ) : (
                <p className="text-muted"></p>
              )}
            </div>
          </div>
        </div>

        <Dashfooter />
      </div>
    </div>
  );
}

export default ActualizarPesos;
