import React, { useCallback, useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  actualizarRol,
  consultarRoles,
} from "../../../../redux/actions/roles_actions";
import "../asignacion_acciones/asignacion-acciones.css";
import { useToasts } from "react-toast-notifications";
import AsignacionHook from "../../../../hooks/asignacion_acciones_hook";

function AsignacionAcciones() {
  const [
    user,
    fetching,
    funciones,
    listRoles,
    switchRol,
    actualizarAsignacion,
    changeValue,
    handleChange,
    buscar,
  ] = AsignacionHook();

  return (
    <div className="crearGuias col-md-9 ms-sm-auto col-lg-10 px-md-4 pt-4">
      <div className="container-fluid pt-4 mb-4 ">
        <h4 className="title-separtor">Asignacion Acciones</h4>
        <hr />
        <div className="card card-body cart-info-data">
          <div className="row ">
            <div className="col-lg-6 ">
              <form>
                <div className="mb-3">
                  <label className="form-label-input">Tipo Usuario</label>
                  <select
                    className="form-select"
                    value={user}
                    onChange={changeValue}
                  >
                    <option value="">Seleccionar Tipo de</option>
                    {listRoles?.map((rol) => (
                      <option key={rol.id} value={rol.rol}>
                        {switchRol(rol.rol)}
                      </option>
                    ))}
                  </select>
                </div>
              </form>
            </div>
          </div>
          {user == "" ? (
            <div className="p-4">Debes seleccionar un Rol</div>
          ) : (
            <div  >
              <h5 className="titleSection-form">Acciones</h5>
              <hr />
              <div className="row mt-2 p-4">
                <div className="col-lg-3 ">
                  <label className="form-label-input">Guias</label>
                  <div className="mt-3">
                    <div className="form-check text-start">
                      <input
                        type="checkbox"
                        className="form-check-input"
                        name="guia_crear"
                        checked={funciones?.g_crear}
                        onChange={() => handleChange("g_crear")}
                      />
                      <label
                        className="form-check-label"
                      >
                        Crear Guias
                      </label>
                    </div>
                    <div className="form-check text-start">
                      <input
                        type="checkbox"
                        className="form-check-input"
                        name="guia_rastrear"
                        checked={funciones?.g_rastrear}
                        onChange={() => handleChange("g_rastrear")}
                      />
                      <label
                        className="form-check-label"
                      >
                        Rastrear Guias
                      </label>
                    </div>

                    <div className="form-check text-start">
                      <input
                        type="checkbox"
                        className="form-check-input"
                        name="guia_anular"
                        checked={funciones?.g_anular}
                        onChange={() => handleChange("g_anular")}
                      />
                      <label
                        className="form-check-label"
                      >
                        Anular Guias
                      </label>
                    </div>
                    <div className="form-check text-start">
                      <input
                        type="checkbox"
                        className="form-check-input"
                        id="customCheck5"
                        name="guia_solicitar_anular"
                        checked={funciones?.g_solicitar}
                        onChange={() => handleChange("g_solicitar")}
                      />
                      <label
                        className="form-check-label"
                      >
                        Solicitar Anulación
                      </label>
                    </div>
                  </div>
                </div>
                <div className="col-lg-3 ">
                  <label className="form-label-input">Manifiestos</label>
                  <div className="mt-3">
                    <div className="form-check text-start">
                      <input
                        type="checkbox"
                        className="form-check-input"
                        name="manifiesto_actualizar_peso"
                        checked={funciones?.m_actualizar}
                        onChange={() => handleChange("m_actualizar")}
                      />
                      <label
                        className="form-check-label"
                      >
                        Actualizar Pesos
                      </label>
                    </div>
                    <div className="form-check text-start">
                      <input
                        type="checkbox"
                        className="form-check-input"
                        name="manifiesto_crear"
                        checked={funciones?.m_crear}
                        onChange={() => handleChange("m_crear")}
                      />
                      <label
                        className="form-check-label"
                      >
                        Crear Manifiestos
                      </label>
                    </div>
                    <div className="form-check text-start">
                      <input
                        type="checkbox"
                        className="form-check-input"
                        name="manifiesto_receptar"
                        checked={funciones?.m_receptar}
                        onChange={() => handleChange("m_receptar")}
                      />
                      <label
                        className="form-check-label"
                      >
                        Receptar Manifiestos
                      </label>
                    </div>
                    <div className="form-check text-start">
                      <input
                        type="checkbox"
                        className="form-check-input"
                        name="manifiesto_buscar"
                        checked={funciones?.m_buscarM}
                        onChange={() => handleChange("m_buscarM")}
                      />
                      <label
                        className="form-check-label"
                      >
                        Buscar Manifiestos
                      </label>
                    </div>
                    <div className="form-check text-start">
                      <input
                        type="checkbox"
                        className="form-check-input"
                        name="manifiesto_asignar_cartero"
                        checked={funciones?.m_asignar}
                        onChange={() => handleChange("m_asignar")}
                      />
                      <label
                        className="form-check-label"
                      >
                        Asignar A Cartero
                      </label>
                    </div>
                    <div className="form-check text-start">
                      <input
                        type="checkbox"
                        className="form-check-input"
                        name="manifiesto_buscar_asignacion"
                        checked={funciones?.m_buscarA}
                        onChange={() => handleChange("m_buscarA")}
                      />
                      <label
                        className="form-check-label"
                      >
                        Buscar Asignaciones
                      </label>
                    </div>
                    <div className="form-check text-start">
                      <input
                        type="checkbox"
                        className="form-check-input"
                        name="manifiesto_reg_correspondencia"
                        checked={funciones?.m_registrar}
                        onChange={() => handleChange("m_registrar")}
                      />
                      <label
                        className="form-check-label"
                      >
                        Registro de Correspondencia (Dar de Baja)
                      </label>
                    </div>
                  </div>
                </div>
                <div className="col-lg-3 ">
                  <label className="form-label-input">
                    Gestion de Usuarios
                  </label>
                  <div className="mt-3">
                    <div className="form-check text-start">
                      <input
                        type="checkbox"
                        className="form-check-input"
                        name="usuario_crear"
                        checked={funciones?.gu_crearU}
                        onChange={() => handleChange("gu_crearU")}
                      />
                      <label
                        className="form-check-label"
                      >
                        Crear Usuarios
                      </label>
                    </div>
                    <div className="form-check text-start">
                      <input
                        type="checkbox"
                        className="form-check-input"
                        name="usuario_rol_crear"
                        checked={funciones?.gu_crearR}
                        onChange={() => handleChange("gu_crearR")}
                      />
                      <label
                        className="form-check-label"
                      >
                        Crear Roles de Usuarios
                      </label>
                    </div>
                    <div className="form-check text-start">
                      <input
                        type="checkbox"
                        className="form-check-input"
                        name="usuario_buscar"
                        checked={funciones?.gu_buscar}
                        onChange={() => handleChange("gu_buscar")}
                      />
                      <label
                        className="form-check-label"
                      >
                        Buscar Usuarios
                      </label>
                    </div>
                    <div className="form-check text-start">
                      <input
                        type="checkbox"
                        className="form-check-input"
                        name="usuario_editar"
                        checked={funciones?.gu_editar}
                        onChange={() => handleChange("gu_editar")}
                      />
                      <label
                        className="form-check-label"
                      >
                        Editar Usuarios
                      </label>
                    </div>
                    <div className="form-check text-start">
                      <input
                        type="checkbox"
                        className="form-check-input"
                        name="usuario_eliminar"
                        checked={funciones?.gu_eliminar}
                        onChange={() => handleChange("gu_eliminar")}
                      />
                      <label
                        className="form-check-label"
                      >
                        Eliminar Usuarios
                      </label>
                    </div>
                  </div>
                </div>
                <div className="col-lg-3 ">
                  <label className="form-label-input">
                    Gestion de Plataforma
                  </label>
                  <div className="mt-3">
                    <div className="form-check text-start">
                      <input
                        type="checkbox"
                        className="form-check-input"
                        name="plataforma_asignacion"
                        checked={funciones?.gp_asignar}
                        onChange={() => handleChange("gp_asignar")}
                      />
                      <label
                        className="form-check-label"
                      >
                        Asignar Acciones
                      </label>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          )}
        </div>

        <div className="card card-body">
          <div className="row">
            <button type="button" className="btn-dt col mx-1">
              Cancelar
            </button>
            <button
              type="button"
              className="btn btn-success col mx-1"
              onClickCapture={actualizarAsignacion}
            >
              Guardar
            </button>
          </div>
        </div>

        {/* <div className="card card-body">
					<div className="row">
						<button type="button" className=" btn-dt-del col mx-1 p-2 bt-a">
							Cancelar
						</button>
						<button type="button" className=" btn-dt-details col mx-1 bt-a">
							Anular
						</button> */}
        {/* <button type="button" className="btn-dt col mx-1">Elimina</button>
                    <button type="button" className="btn btn-success col mx-1">Imprimir</button> */}
        {/* </div>
				</div> */}
      </div>
    </div>
  );
}

export default AsignacionAcciones;
