import React from "react";

function FormPesos({
  numPiezas = 0,
  values,
  agregarAListaActualizados,
  ActivePesoVol,
  handleChangeValue,
}) {
  let listaItems = [];
  for (let index = 0; index < numPiezas; index++) {
    listaItems.push(
      <div key={`datos_${index}`} className="card card-body">
        <div className="row ">
          <div className="col-sm-12 col-md-5">
            <div className="mb-3">
              <h5 className="titleSection-form mt-2">Pieza {index + 1}</h5>
              <hr />
              <div className="input-group">
                <input
                  id={`input-peso${index}`}
                  className="form-control"
                  type="text"
                  className="form-control"
                  placeholder="0.00 kg"
                  name="pesototal"
                  value={values?.piezas[index]?.pesototal}
                  onChange={(e) => handleChangeValue(e, index)}
                 
                  required
                />
                {values?.piezas?.length > 1 ? (
                  <></>
                ) : (
                  <button
                    className="btn-solid-secondary px-4 "
                    type="submit"
                    onClick={agregarAListaActualizados}
                  >
                    Peso
                  </button>
                )}
              </div>

              {values?.piezas[index]?.edit === false ? (
                <></>
              ) : (
                <div className="nav-item text-start mt-2">
                  <a
                    className="nav-link"
                    href="#"
                    data-bs-toggle="collapse"
                    href={`#volumetrico${index}`}
                    role="button"
                    aria-expanded={values?.piezas[index]?.pesoVol}
                    aria-controls={`volumetrico${index}`}
                    onClick={() => ActivePesoVol(index)}
                  >
                    Agregar Peso Volumetrico
                  </a>
                </div>
              )}
            </div>
          </div>
        </div>

        <div
          className="collapse w-100 align-items-start"
          id={`volumetrico${index}`}
        >
          <div>
            <div className="row ">
              <div className="col-sm-12 col-md-4">
                <div className="mb-3">
                  <label className="form-label-input">Largo</label>
                  <input
                    className="form-control"
                    type="number"
                    placeholder="0.00 Kg"
                    min={0}
                    name="largo"
                    onChange={(e) => handleChangeValue(e, index)}
                    value={values?.piezas[index]?.largo}
                  />
                </div>
              </div>
              <div className="col-sm-12 col-md-4">
                <div className="mb-3">
                  <label className="form-label-input">Ancho</label>
                  <input
                    className="form-control"
                    type="number"
                    placeholder="0.00 Kg"
                    min={0}
                    name="ancho"
                    onChange={(e) => handleChangeValue(e, index)}
                    value={values?.piezas[index]?.ancho}
                  />
                </div>
              </div>
              <div className="col-sm-12 col-md-4">
                <div className="mb-3">
                  <label className="form-label-input">Alto</label>
                  <input
                    className="form-control"
                    minLength={0}
                    debounceTimeout={500}
                    type="number"
                    placeholder="0.00 Kg"
                    name="alto"
                    min={0}
                    onChange={(e) => handleChangeValue(e, index)}
                    value={values?.piezas[index]?.alto}
                  />
                </div>
              </div>
            </div>
            {values?.piezas[index]?.edit == true && numPiezas < 1 ? (
              <div className="row mt-2">
                <div className="col-sm-12 col-md-12">
                  <button
                    type="submit"
                    className="btn-solid-secondary w-100 p-2 mb-1"
                  >
                    Editar Peso
                  </button>
                </div>
              </div>
            ) : (
              <></>
            )}
          </div>
        </div>
      </div>
    );
  }
  return <div>{listaItems.map((e) => e)}</div>;
}

export default FormPesos;
