import React, { useState } from "react";
import { UserFormCrearGuias } from "../../../../hooks/user_form_crearGuias";
import { useSelector } from "react-redux";
function PiezaItem({
  piezas = 0,
  values,
  clicPesoVol,
  rolUser,
  onchangePiezas,
  piezasdata,
}) {
  let items = [];

  for (let index = 0; index < piezas; index++) {
    items.push(
      <div key={`item_peso_${index}`} className="pesoitem">
        <h5 className="titleSection-form">{`Pieza ${index + 1}`}</h5>
        <hr />
        <div className="card card-body">
          <div className="row ml-2">
            <div className="col-lg-6  border-separator  ">
              <div className=" text-end">
                <label className="form-label-input">Contenido</label>
                <textarea
                  className={`form-control input-UPER text-break ${
                    piezasdata[index]?.contenido?.length >= 1 ? "complete-indo" : ""
                  }`}
                  rows={5}
                  placeholder="Describe el contenido del envío, maximo 65 caracteres"
                  onChange={(e) => onchangePiezas(e, index)}
                  value={piezasdata[index]?.contenido}
                  maxlength="65"
                  minlength="5"
                  name={`contenido`}
                  required
                />
                {piezasdata[index]?.contenido.length === 65 ? (
                  <span className="mr-2 mt-2 text-muted sized-max">
                    Maximo de Caracteres
                  </span>
                ) : (
                  <></>
                )}
                <span class="badge bg-secondary">
                  {piezasdata[index]?.contenido?.length ?? 0}/65
                </span>
              </div>
            </div>

            <div className="col-lg-6">
              <div className="mb-3">
                <label className="form-label-input">Peso</label>
                <input
                  className={`form-control input-UPER ${
                    piezasdata[index]?.pesototal?.length >= 1 ? "complete-indo" : ""
                  }`}
                  type="text"
                  oninput="this.value = this.value.replace(/[^0-9.]/g, '').replace(/(\..*?)\..*/g, '$1');"
                  placeholder="0.00 Kg"
                  name="pesototal"
                  min={0}
                  onChange={(e) => onchangePiezas(e, index)}
                  value={piezasdata[index]?.pesototal}
                  readOnly={
                    piezasdata[index]?.pesoVol ||
                    rolUser === "Corporativo" ||
                    values?.tipo === "documento"
                  }
                />
              </div>
              {values?.tipo === "documento" ||
              values?.tipo === "" ||
              rolUser == "Corporativo" ? (
                <></>
              ) : (
                <div className="mb-3 text-start">
                  <a
                    className="nav-link"
                    href={`#pesosvlcrear${index}`}
                    data-bs-toggle="collapse"
                    role="button"
                    aria-expanded="false"
                    aria-controls={`pesosvlcrear${index}`}
                    onClick={() => clicPesoVol(index)}
                  >
                    Agregar Peso Volumétrico
                  </a>
                </div>
              )}
              <div
                className="collapse w-100 align-items-start"
                id={`pesosvlcrear${index}`}
              >
                <div className="list-group-flush">
                  <div className="mb-3">
                    <label className="form-label-input">
                      Largo <span className="text-muted">(cm)</span>
                    </label>
                    <input
                      className={`form-control input-UPER ${
                        piezasdata[index]?.largo?.length >= 1
                          ? "complete-indo"
                          : ""
                      }`}
                      type="number"
                      placeholder="0.00 Kg"
                      name="largo"
                      onChange={(e) => onchangePiezas(e, index)}
                      value={piezasdata[index]?.largo}
                      min={0}
                      //   required
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">
                      Ancho <span className="text-muted">(cm)</span>
                    </label>
                    <input
                      className={`form-control input-UPER ${
                        piezasdata[index]?.ancho >= 1 ? "complete-indo" : ""
                      }`}
                      type="number"
                      placeholder="0.00 Kg"
                      name="ancho"
                      onChange={(e) => onchangePiezas(e, index)}
                      value={piezasdata[index]?.ancho}
                      min={0}
                      //   required
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label-input">
                      Alto <span className="text-muted">(cm)</span>
                    </label>
                    <input
                      className={`form-control input-UPER ${
                        piezasdata[index]?.alto?.length >= 1
                          ? "complete-indo"
                          : ""
                      }`}
                      type="number"
                      placeholder="0.00 Kg"
                      name="alto"
                      onChange={(e) => onchangePiezas(e, index)}
                      value={piezasdata[index]?.alto}
                      min={0}
                      //   required
                    />
                  </div>
                </div>
              </div>
              <div className="mb-3">
                <label className="form-label-input">Valor Declarado</label>
                <input
                  type="number"
                  className={`form-control input-UPER ${
                    piezasdata[index]?.v_declarado?.length >= 1
                      ? "complete-indo"
                      : ""
                  }`}
                  placeholder="$ 0.00"
                  name="v_declarado"
                  onChange={(e) => onchangePiezas(e, index)}
                  value={piezasdata[index]?.v_declarado}
                />
              </div>
              <div className="mb-3">
                <label className="form-label-input">Valor Asegurado</label>
                <input
                  name="v_asegurado"
                  value={piezasdata[index]?.v_asegurado}
                  type="text"
                  className={`form-control input-UPER ${
                    piezasdata[index]?.v_asegurado?.length >= 1
                      ? "complete-indo"
                      : ""
                  }`}
                  placeholder="$ 0.00"
                  readOnly
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
  return <div>{items.map((e) => e)}</div>;
}

export default PiezaItem;
