import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useToasts } from "react-toast-notifications";
import { piezaFormat } from "../constants/constants";
import { truncate } from "../helpers/helper";
import {
  currentGuiaDetail,
  findGuiaPesos,
  currentEditGuia,
  limpiarListaGuiasPesos,
  currentGuiaDetailPesos,
  actualizarPesoGuias,
  addGuiaConPesoActualizado,
  updateGuiaPesoEdit,
  deletefromPesosActualizados,
  setEditGuia,
  autoSearchPesos,
} from "../redux/actions/guias_actions";

function ActualizarPesosHook() {
  const dispatch = useDispatch();
  const { addToast } = useToasts();
  const initValues = {
    idGuia: "",
    pesoVol: false,
    onEdit: false,
  };
  const {
    guias: {
      ListGuiasParaPeso,
      fetching,
      msgError,
      currentDetailGuiaPesos,
      ListGuiasPesoActualizado,
      autosearchPesos,
    },
    auth: { dataProfile },
  } = useSelector((state) => state);

  const rolUser =
    dataProfile?.rol === "admin"
      ? "Administrador"
      : dataProfile?.rol === "puntoVenta"
      ? "Punto de venta"
      : dataProfile?.rol === "sucursal"
      ? "Sucursal"
      : "Corporativo";

  const [values, setvalues] = useState({
    piezas: ListGuiasParaPeso?.piezas ?? [],
    editpesos: false,
  });

  const [numGuia, setNumGuia] = useState("");

  const errorSms = (sms) => toastSms(`${sms}`, "error");
  const toastSms = (text, type) => {
    return addToast(text, {
      appearance: type,
      autoDismiss: true,
      autoDismissTimeout: 3000,
    });
  };

  const handleOnBlur = (e) => {
    if (e.target.value !== "") {
      e.preventDefault();
    }
    const successSms = () => toastSms(`Guia Agregada`, "success");
    setNumGuia(e?.target?.value.trim());
    if (e?.target?.value.trim()) {
      if (!e?.target?.value.trim()) {
        toastSms("debes ingresar un valor", "info");
        document.getElementById("searchGuia").focus();
      } else {
        let res = e?.target?.value.replace(/ /g, "");
        let listResividos = ListGuiasPesoActualizado.filter(
          (item) => item.idGuia === res
        ).length;
        if (ListGuiasParaPeso?.idGuia === `${res}`) {
          toastSms(`Esta guia ya fue seleccionada`, "info");
          setvalues({ ...values, idGuia: "" });
          setTimeout(() => {
            document.getElementById("searchGuia").focus();
          }, 400);
        } else if (listResividos >= 1) {
          toastSms(`Esta guia ya fue actualizada`, "info");
          setvalues({ ...values, idGuia: "" });
          setTimeout(() => {
            document.getElementById("searchGuia").focus();
          }, 400);
        } else {
          dispatch(findGuiaPesos(res, errorSms, successSms));
        }
      }
    } else {
      toastSms("debes ingresar un valor", "info");
      document.getElementById("searchGuia").focus();
    }
  };
  const handrerchangeSearch = (e) => {
    setvalues({
      ...values,
      [e.target.name]: e?.target?.value,
    });
  };

  const searchGuiaId = () => {
    const successSms = () => toastSms(`Guia Agregada`, "success");

    if (values?.idGuia.trim()) {
      if (!values?.idGuia.trim()) {
        toastSms("debes ingresar un valor", "info");
        document.getElementById("searchGuia").focus();
      } else {
        let res = values?.idGuia.replace(/ /g, "");
        let listResividos = ListGuiasPesoActualizado.filter(
          (item) => item.idGuia === res
        ).length;
        if (ListGuiasParaPeso?.idGuia === `${res}`) {
          toastSms(`Esta guia ya fue seleccionada`, "info");
          setvalues({ ...values, idGuia: "" });
          setTimeout(() => {
            document.getElementById("searchGuia").focus();
          }, 400);
        } else if (listResividos >= 1) {
          toastSms(`Esta guia ya fue actualizada`, "info");
          setvalues({ ...values, idGuia: "" });
          setTimeout(() => {
            document.getElementById("searchGuia").focus();
          }, 400);
        } else {
          dispatch(findGuiaPesos(res, errorSms, successSms));
        }
      }
    } else {
      toastSms("debes ingresar un valor", "info");
      document.getElementById("searchGuia").focus();
    }
  };
  const handleChangeValue = (e, index) => {
    if (e.target.value !== "") {
      e.preventDefault();
    }
    let piezas = [...values?.piezas];
    piezas[index] = { ...piezas[index], [e.target.name]: e.target.value };
    setvalues({ ...values, piezas }); // setvalues({

    if (values.piezas[index]?.pesoVol) {
      let total =
        (parseFloat(values.piezas[index]?.largo ?? 0.0) *
          parseFloat(values.piezas[index]?.ancho ?? 0.0) *
          parseFloat(values.piezas[index]?.alto ?? 0.0)) /
        6000;
      piezas[index] = { ...piezas[index], pesototal: truncate(total, 2)  };
    }
  };
  const ActivePesoVol = (index) => {
    let piezas = [...values?.piezas];
    if (values.piezas[index]?.pesoVol === false) {
      piezas[index] = {
        ...piezas[index],
        pesoVol: !piezas[index]?.pesoVol,
        pesototal: "",
        largo: "",
        ancho: "",
        alto: "",
      };
    } else {
      piezas[index] = {
        ...piezas[index],
        pesoVol: !piezas[index]?.pesoVol,
      };
    }
    setvalues({ ...values, piezas });
  };

  const agregarAListaActualizados = (e) => {
    if (e.target.value !== "") {
      e.preventDefault();
    }
    // // console.log(values?.piezas);
    let listSinPeso = values?.piezas?.filter(
      (item) =>
        item.pesototal === "" ||
        item.pesototal === "0" ||
        item.pesototal === undefined
    );
    // console.log(listSinPeso.length);

    if (listSinPeso.length > 0) {
      toastSms("Debes ingresar un peso", "error");
    } else {
      // console.log("OKOKOKOOKOKOKKOKO");
      let listTempPiezas = [];
      values?.piezas.map((e) => {
        let tempPiezxa = {
          alto: e?.alto ?? "",
          ancho: e?.ancho ?? "",
          largo: e?.largo ?? "",
          pesototal: e?.pesototal ?? "",
          contenido: e?.contenido ?? "",
          pesoVol: e?.pesoVol ?? false,
          v_declarado: e?.v_declarado ?? "",
          v_asegurado: e?.v_asegurado ?? "",
          edit: false,
        };

        listTempPiezas = [...listTempPiezas, tempPiezxa];
      });
      let tempGuia = {
        ...ListGuiasParaPeso,
        piezas: listTempPiezas,
        updatePeso: {
          fecha: Date.now().toString(),
          rol: rolUser,
          usuario: dataProfile?.email,
        },
        estado: "peso actualizado",
      };
      dispatch(addGuiaConPesoActualizado(tempGuia));
      toastSms("Peso Actualizado", "success");
      setTimeout(() => {
        document.getElementById("searchGuia").focus();
      }, 450);
    }
  };
  const editGuiaPeso = (guia) => {
    let pesos = [];
    guia?.piezas.map((e) => {
      let temppeso = {
        ...e,
        edit: true,
      };
      pesos.push(temppeso);
    });
    setvalues({
      ...values,
      editpesos: !values?.editpesos,
      piezas: pesos,
      guia: guia,
    });
  };

  const updateGuiaonList = (e) => {
    if (e.target.value !== "") {
      e.preventDefault();
    }
    let temp = {
      ...values.guia,
      piezas: values?.piezas,
    };
    // console.log(temp);
    dispatch(updateGuiaPesoEdit(temp, ListGuiasPesoActualizado));

    setvalues({
      ...values,
      piezas: [],
      guia: {},
    });
    document.getElementById("searchGuia").focus();
  };
  const eliminardeListaActualizados = (guia) => {
    let listtemp = ListGuiasPesoActualizado.filter(
      (guiaitem) => guiaitem.idGuia !== guia.idGuia
    );

    dispatch(deletefromPesosActualizados(listtemp));
    toastSms("Item eliminado de la lista", "info");
  };

  const updateGuiasFirebase = () => {
    const errorSms = () => toastSms(`${msgError}`, "error");
    const successSms = () => toastSms("Pesos Actualizados", "success");
    dispatch(
      actualizarPesoGuias(ListGuiasPesoActualizado, successSms, errorSms)
    );
    setvalues({});
  };

  const changeAutosearch = () => {
    dispatch(autoSearchPesos(!autosearchPesos));
  };
  return [
    ListGuiasParaPeso,
    fetching,
    currentDetailGuiaPesos,
    ListGuiasPesoActualizado,
    values,
    autosearchPesos,
    searchGuiaId,
    changeAutosearch,
    agregarAListaActualizados,
    ActivePesoVol,
    handleChangeValue,
    editGuiaPeso,
    updateGuiaonList,
    eliminardeListaActualizados,
    updateGuiasFirebase,
    handleOnBlur,
    handrerchangeSearch,
  ];
}

export default ActualizarPesosHook;
