import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { v1 as uuid } from "uuid";
import { guiaTest, listTextGuias } from "../constants/constants";
import { localidadesList } from "../constants/db_provincias";
import { savePDF, truncate } from "../helpers/helper";
import { debounce } from "lodash.debounce";

import {
  addGuia,
  guardarGuias,
  limpiarGuia,
  salveToPrint,
  updateGuia,
} from "../redux/actions/guias_actions";
import { useToasts } from "react-toast-notifications";
import { db } from "../firebase";

export function UserFormCrearGuias(initialValues) {
  const {
    guias: { currentEditGuia, guiasAgregadas, msgError, ListPrintGuias },
    auth: { dataProfile },
  } = useSelector((state) => state);
  const dispatch = useDispatch();
  const { addToast } = useToasts();
  const rolUser =
    dataProfile?.rol === "admin"
      ? "Administrador"
      : dataProfile?.rol === "puntoVenta"
      ? "Punto de venta"
      : dataProfile?.rol === "sucursal"
      ? "Sucursal"
      : "Corporativo";

  const [values, setValues] = useState(initialValues);

  const handledChange = (e) => {
    let name = e.target.name.split(" ");
    if (name[0] === "remitente") {
      let remitente = {
        ...values.remitente,
        [name[1]]: e.target.value,
      };
      setValues({ ...values, remitente: remitente });
    } else if (name[0] === "destinatario") {
      let destinatario = {
        ...values.destinatario,
        [name[1]]: e.target.value,
      };
      setValues({ ...values, destinatario: destinatario });
    } else if (name.length === 1) {
      let valuestemp;
      valuestemp = {
        ...values,
        [name[0]]: e.target.value,
      };
      if (name[0] === "tipo") {
        if (e.target.value === "documento") {
          let piezas = [...values?.piezas];
          piezas[0] = { ...piezas[0], pesototal: 0.5, contenido: "" };
          valuestemp = {
            ...values,
            [name[0]]: e.target.value,
            numPiezas: 1,
            piezas: piezas,
          };
        } else {
          let piezas = [...values?.piezas];
          piezas[0] = {
            ...piezas[0],
            pesototal: "",
            contenido: "",
            largo: "",
            alto: "",
            ancho: "",
            v_asegurado: "",
            v_declarado: "",
          };
          valuestemp = {
            ...values,
            [name[0]]: e.target.value,
            numPiezas: 1,
            piezas: piezas,
          };
        }
      }
      if (e.target.name === "numPiezas" && values?.numPiezas === "") {
        valuestemp = {
          ...values,
          numPiezas: e.target.value,
          piezas: [],
        };
      }
      setValues(valuestemp);
    }
  };
  const onchangePiezas = (e, index) => {
     if (e.target.value !== "") {
      e.preventDefault();
    }
    let piezas = [...values?.piezas];

    if (e.target.name === "v_declarado") {
      let asegurado = parseFloat(e.target.value) * 0.005;
      let datasend = truncate(asegurado, 2);
      piezas[index] = {
        ...piezas[index],
        v_declarado: e.target.value,
        v_asegurado: datasend.toString(),
      };
    } else if (piezas[index]?.pesoVol === true) {
      let total =
        (parseFloat(piezas[index]?.largo ?? 0.0) *
          parseFloat(piezas[index]?.ancho ?? 0.0) *
          parseFloat(piezas[index]?.alto ?? 0.0)) /
        6000;
      piezas[index] = {
        ...piezas[index],
        [e.target.name]: e.target.value,
        pesototal: truncate(total, 3).toString(),
      };
    } else {
      piezas[index] = { ...piezas[index], [e.target.name]: e.target.value };
    }
    setValues({ ...values, piezas });
  };

  const clicPesoVol = (index) => {
    let piezas = [...values?.piezas];
    piezas[index] = { ...piezas[index], pesoVol: !piezas[index]?.pesoVol };
    setValues({ ...values, piezas });
    if (piezas[index]?.pesoVol === false) {
      piezas[index] = {
        ...piezas[index],
        pesoVol: "",
        largo: "",
        alto: "",
        ancho: "",
      };
      setValues({ ...values, piezas });
    }
  };

  const SaveGuiaStorage = (e) => {
    const success = () => toastSms("Guias Agregada a la Lista", "success");
    const info = () => toastSms("Guia Editada", "info");
     if (e.target.value !== "") {
      e.preventDefault();
    }
    if (currentEditGuia?.destinatario?.nombres === "") {
      let tempvalues = {
        ...values,
        idGuia: uuid(),
        estado: "creado",
        creacion: {
          fecha: Date.now().toString(),
          usuario: dataProfile?.email,
          rol: rolUser?.toString(),
        },
      };

      setValues(tempvalues);
      dispatch(addGuia(tempvalues));
      setValues(initialValues);
      success();
    } else {
      values.idGuia = currentEditGuia.idGuia;
      values.fechaActual = currentEditGuia.fechaActual;
      dispatch(updateGuia(values, values.idGuia, guiasAgregadas));
      setValues(initialValues);
      info();
    }
  };
  const toastSms = (text, type) => {
    return addToast(text, {
      appearance: type,
      autoDismiss: true,
      autoDismissTimeout: 1000,
    });
  };

  const saveGuia = () => {
    window.print();
  };

  const setGuiasDb = () => {
    const success = () => toastSms("Guias Agregadas con exito", "success");
    const errorsms = () => toastSms(`${msgError}!`, "error");
    const printguias = () => window.print();
    dispatch(guardarGuias(guiasAgregadas, success, errorsms, printguias));
    // setTimeout(() => {

    // }, 1300);
  };

  const printLog = () => {
    saveGuia();
  };

  return [
    rolUser,
    values,
    printLog,
    setGuiasDb,
    clicPesoVol,
    SaveGuiaStorage,
    setValues,
    handledChange,
    dataProfile,
    onchangePiezas,
  ];
}
