export function generateListGuias(ListofGuias) {
  let tempList = [];
  ListofGuias.map((guia) => {
    //   ListofGuias.map((guia) => {
    if (guia?.numPiezas > 1) {
      let fracciontemp = 0;
      let totalpesoguias = 0;
      guia.piezas.map((e) => {
        totalpesoguias = parseFloat(e.pesototal) + totalpesoguias;
      });
      let tempPieza = {
        alto: "",
        ancho: "",
        contenido: "",
        largo: "",
        pesoVol: false,
        pesototal: "",
        v_asegurado: "",
        v_declarado: "",
      };
      guia = { ...guia, totalguias: totalpesoguias, pieza: tempPieza };
      tempList = [...tempList, guia];
      tempList = [...tempList, guia];
      for (let index = 0; index < guia.numPiezas; index++) {
        fracciontemp = fracciontemp + 1;
        let piezatemp = guia?.piezas[index];
        guia = {
          ...guia,
          fraccion: fracciontemp,
          pieza: piezatemp,
          piezaIndex: index,
        };
        if (index + 1 === guia?.numPiezas) {
          guia = { ...guia, islast: true, showSpace: false };
        }
        tempList = [...tempList, guia];
      }
    } else {
      let copyguias = 3;
      let piezatemp = guia?.piezas[0];
      let tempPieza = {
        alto: "",
        ancho: "",
        contenido: "",
        largo: "",
        pesoVol: false,
        pesototal: "",
        v_asegurado: "",
        v_declarado: "",
      };
      tempPieza = {
        ...piezatemp,
      };
      for (let index = 1; index < copyguias; index++) {
        guia = { ...guia, pieza: tempPieza };
        tempList = [...tempList, guia];
        if (copyguias - 1 === index) {
          guia = { ...guia, islast: true, showSpace: false };
          tempList = [...tempList, guia];
        }
      }
    }
  });
  return tempList;
}

